.users-container {
    margin-top: 150px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.user {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px;
    width: 250px;
    height: 300px;
    background: #d8d8d8;
}

.delete {
    border-radius: 20px;
    font-size: 14px;
    font-weight: 500;
    border: none;
    text-decoration: none;
    width: 150px;
    padding: 10px;
    background: #ff8181;
}

.delete:hover {
    cursor: pointer;
    background: #ff5b5b; 
}

.edit {
    border-radius: 20px;
    margin: 10px;
    font-size: 14px;
    font-weight: 500;
    border: none;
    text-decoration: none;
    width: 150px;
    padding: 10px;
    background: #8192ff;
}

.edit:hover {
    cursor: pointer;
    background: #546aff; 
}