.form {
    margin: 25px;
    margin-left: 41%;
    width: 260px;
    height: 350px;
    background: #d8d8d8;
    display: flex;
    flex-direction: column;
}

.container {
    padding: 10px;
}

.label {
    font-size: 22px;
    font-weight: 600;
    margin-right: 5px;
}

.form-input {
    border: none;
    text-decoration: none;
    padding: 4px;
    background: #fff;
}

.form-submit {
    font-size: 15px;
    font-weight: 550;
    margin-top: 30%;
    border: none;
    text-decoration: none;
    width: 175px;
    padding: 10px;
}

.form-submit:hover {
    cursor: pointer;
    background: #fff;
}